import React, { Component } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { WebNav } from "../../components/navbar";
import { Footer } from '../../components/footer';
import { BsChevronDoubleDown } from "react-icons/bs";

/** image */
import logo from "../../img/logo.png";
import nckuLogo from "../../img/ncku_logo.png";
import nnImg from "../../img/nn.webp";
import bmiImg from "../../img/bmi.webp";


class Home extends Component {
  render() {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover />
        <Slogan />
        <About />
        <Future />
        <JoinUs />
        <Footer />
      </React.StrictMode>
    );
  }
}

export { Home };

/** 背景預計使用word cloud*/
function Cover() {
  return (
    <div>
      <div className='cover'>
        <div className='subtitle'>
          {/* <img src={nckuLogo} style={{height:'3rem'}} /> */}
          <p>NCKU</p>
        </div>
        <div className='title'>
          <p>Neural Computation<br />Laboratory</p>
        </div>
        <div className='down'>
          <BsChevronDoubleDown />
        </div>
      </div>
    </div>

  )

}

function Slogan(params) {
  return (
    <div className='sloganBack'>
      <p className='slogan'>        
        我們專門培養資料科學家<br />
        We train students as data scientist
      </p>
    </div>
  )
}


function About(params) {
  return (
    <div className='homePage'>
      <Container fluid className='about'>
        <Row className=''>
          <Col xs={12} md={6}>
            <div className='card'>
              <div className='subTitle mt-5'>
                <p>Research</p>
              </div>
              <div className='title'>
                <p>類神經網路<br />Neural Network</p>
              </div>
              <div className='imagebox'>
                <img className='h-100 w-100 p-5' src={nnImg} />
              </div>
            </div>
          </Col>
          <Col xs={12} md={6}>
            <div className='card'>
              <div className='subTitle mt-5'>
                <p>Research</p>
              </div>
              <div className='title'>
                <p>侵入式腦機介面<br />Invasive Brain Machine Interface</p>
              </div>
              <div className='imagebox'>
                <img className='h-100 w-100 p-5' src={bmiImg} />
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  )
}

function Future(params) {
  return (
    <Container fluid>

    </Container>
  )
}

function JoinUs(params) {
  return (
    <div className='homePage'>
      <Container fluid>
        <Row>
          <Col xs={12}>
            <div className='joinUs'>

            </div>
          </Col>
        </Row>
      </Container>
    </div>

  )
}
