import React, { Component, useState, useEffect } from "react";
import { WebNav } from "../../components/navbar";
import { CoverHalf } from "../../components/coverHalf";
import { Footer } from "../../components/footer";
import { Col, Container, Row, Card, Button } from "react-bootstrap";
import Loader from "react-loader-spinner";
import { readRemoteFile } from "react-papaparse";

import RAWDATA from "./researchData";

function Research(params) {
  const [csvData, setCsvData] = useState(RAWDATA);
  const [getData, setGetData] = useState(false);

  useEffect(() => {
    if (!getData) {
      const url =
        "https://docs.google.com/spreadsheets/d/e/2PACX-1vTXvqaajxZbMVckzmBZ4k5pWrLOsMPmoCWwOi4w1sgYeV_Mfi7BLfvCaLouxDNubk_tUBJXwyM9DDOq/pub?gid=0&single=true&output=csv";
      readRemoteFile(url, {
        complete: (results) => {
          console.log("Results:", results);
          var newCsvData = [];
          for (let i = 1; i < results.data.length; i++) {
            var data = {};
            for (let j = 0; j < results.data[0].length; j++) {
              data[results.data[0][j]] = results.data[i][j];
            }
            newCsvData.push(data);
          }
          console.log(newCsvData);
          setCsvData(newCsvData);
          setGetData(true);
        },
      });
    }
  }, [getData]);

  if (getData) {
    return (
      <React.StrictMode>
        <WebNav />
        <CoverHalf />
        <ResearchSection data={csvData} />
        <Footer />
      </React.StrictMode>
    );
  } else {
    return (
      <React.StrictMode>
        <WebNav />
        <CoverHalf />
        <div className="text-center my-5">
          <Loader type="Grid" color="#403E3E" height={80} width={80} />
        </div>
        <Footer />
      </React.StrictMode>
    );
  }
}

export { Research };

function ResearchSection(props) {
  const [showIntro, setShowIntro] = useState(false);
  const [selectProject, setSelectProject] = useState("");
  const [introData, setIntroData] = useState();

  let rawData = props.data;

  useEffect(() => {
    if (selectProject === "") return;

    let newData = {
      projectName: selectProject,
      publish: "",
      purpose: "",
      challenge: "",
      method: "",
      result: "",
      application: "",
      image: "",
      imageDescription: "",
    };

    for (let i = 0; i < rawData.length; i++) {
      if (rawData[i].projectName === selectProject) {
        newData.publish = rawData[i].publish;
        newData.purpose = rawData[i].purpose;
        newData.challenge = rawData[i].challenge;
        newData.method = rawData[i].method;
        newData.result = rawData[i].result;
        newData.application = rawData[i].application;
        newData.image = rawData[i].image;
        newData.imageDescription = rawData[i].imageDescription;
      }
    }

    setIntroData(newData);
    setShowIntro(true);
  }, [selectProject]);

  if (showIntro) {
    return (
      <IntroductionPage
        data={introData}
        onClose={() => {
          setShowIntro(false);
        }}
      />
    );
  } else {
    return (
      <Container>
        <Row>
          {rawData.map((d) => (
            <Col xs={12} md={4} className="mt-5">
              <Card className="w-100">
                <Card.Img
                  variant="top"
                  src={d.image}
                  style={{ maxHeight: "10rem", overflow: "hidden" }}
                />
                <Card.Body style={{ minHeight: "10rem", position: "relative" }}>
                  <Card.Title>{d.projectName}</Card.Title>
                  <Button
                    className=""
                    style={{ position: "absolute", bottom: "1rem" }}
                    variant="outline-dark"
                    onClick={() => {
                      setSelectProject(d.projectName);
                    }}
                  >
                    more
                  </Button>
                </Card.Body>
              </Card>
            </Col>
          ))}
        </Row>
      </Container>
    );
  }
}

function IntroductionPage(props) {
  let data = props.data;

  return (
    <React.StrictMode>
      <div className="research">
        <div className="introPage">
          <Container>
            <Row>
              <div
                className="d-block"
                style={{ width: "100%", height: "5rem", marginLeft: "1rem" }}
              >
                <Button
                  variant="dark"
                  onClick={() => {
                    props.onClose();
                  }}
                >
                  回列表
                </Button>
              </div>
            </Row>
            <Row>
              <Col xs={12}>
                <div className="projectName">
                  <p>{data.projectName}</p>
                </div>
                <hr />
              </Col>
            </Row>

            {(() => {
              if (data.publish != "") {
                return (
                  <Row className="justify-content-md-center">
                    <Col xs={12} md={10}>
                      <div className="description">
                        <p className="title">發表</p>
                        <p className="text">{data.publish}</p>
                      </div>
                    </Col>
                  </Row>
                );
              }
            })()}
            {(() => {
              if (data.purpose != "") {
                return (
                  <Row className="justify-content-md-center">
                    <Col xs={12} md={10}>
                      <div className="description">
                        <p className="title">目的</p>
                        <p className="text">{data.purpose}</p>
                      </div>
                    </Col>
                  </Row>
                );
              }
            })()}
            {(() => {
              if (data.challenge != "") {
                return (
                  <Row className="justify-content-md-center">
                    <Col xs={12} md={10}>
                      <div className="description">
                        <p className="title">挑戰</p>
                        <p className="text">{data.challenge}</p>
                      </div>
                    </Col>
                  </Row>
                );
              }
            })()}
            {(() => {
              if (data.method != "") {
                return (
                  <Row className="justify-content-md-center">
                    <Col xs={12} md={10}>
                      <div className="description">
                        <p className="title">方法</p>
                        <p className="text">{data.method}</p>
                      </div>
                    </Col>
                  </Row>
                );
              }
            })()}
            {(() => {
              if (data.result != "") {
                return (
                  <Row className="justify-content-md-center">
                    <Col xs={12} md={10}>
                      <div className="description">
                        <p className="title">結果</p>
                        <p className="text">{data.result}</p>
                      </div>
                    </Col>
                  </Row>
                );
              }
            })()}
            {(() => {
              if (data.application != "") {
                return (
                  <Row className="justify-content-md-center">
                    <Col xs={12} md={10}>
                      <div className="description">
                        <p className="title">應用</p>
                        <p className="text">{data.application}</p>
                      </div>
                    </Col>
                  </Row>
                );
              }
            })()}
            <Row className="justify-content-md-center">
              <Col xs={12} md={10}>
                <div className="image">
                  <p className="title">示意圖</p>
                  <img src={data.image} />
                  <p className="text">{data.imageDescription}</p>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    </React.StrictMode>
  );
}
