import React, { Component, useState, useEffect } from "react";
import { readRemoteFile } from "react-papaparse";
import { WebNav } from "../../components/navbar";
import { CoverHalf } from "../../components/coverHalf";
import { Footer } from '../../components/footer';
import { Container, Row, Col, Button } from "react-bootstrap";
import Loader from 'react-loader-spinner'

function Publications(params) {
  const [paperData, setPaperData] = useState([]);
  const [getData, setGetData] = useState(false);

  useEffect(() => {
    if (!getData) {
      const url =
        "https://docs.google.com/spreadsheets/d/e/2PACX-1vSKlQLD6RuZOD0A8EgBIsNpBv9bF3-Da5vX-iKrLUt4gGL3W22bpla8P4YVbrt5f4eFFGK4xKNtEoyD/pub?output=csv";
      readRemoteFile(url, {
        complete: (results) => {
          // console.log("Results:", results);
          setPaperData(results.data);
          setGetData(true);
        },
      });
    }
  }, [getData]);

  if (getData) {
    return (
      <React.StrictMode>
        <WebNav />
        <CoverHalf />
        <Topic />
        <PublicSection data={paperData} />
        <Footer />
      </React.StrictMode>
    );
  } else {
    return (
      <React.StrictMode>
        <WebNav />
        <CoverHalf />
        <Topic />
        <div className='text-center my-5'>
          <Loader type="Grid" color="#403E3E" height={80} width={80} />
        </div>
        <Footer />
      </React.StrictMode>
    )
  }

}

export { Publications };

function Topic() {
  return (
    <div className="topicSection">
      <div className="title">
        <p>Publications</p>
      </div>
    </div>
  );
}

function SelectBtn(props) {
  return (
    <Container>
      <Row>
        <Col xs={4}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => {
              props.selectChange("papers");
            }}
          >
            <p>期刊論文</p>
          </Button>
        </Col>
        <Col xs={4}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => {
              props.selectChange("proceedings");
            }}
          >
            <p>會議論文</p>
          </Button>
        </Col>
        <Col xs={4}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => {
              props.selectChange("bookPaper");
            }}
          >
            <p>專書論文</p>
          </Button>
        </Col>
      </Row>
    </Container>
  );
}

function PublicationsList(props) {
  return (
    <Container>
      <Row>
        {props.list.map((p) => (
          <Col xs={12}>
            <PublicationsCard title={p.title} author={p.author} at={p.at} />
          </Col>
        ))}
      </Row>
    </Container>
  );
}

function PublicationsCard(props) {
  return (
    <React.StrictMode>
      <a
        href={"https://scholar.google.com/scholar?hl=zh-TW&q=" + props.title}
        target="_blank"
        style={{ textDecoration: "none" }}
      >
        <div className="publications-card">
          <div className="title">
            <p className="m-0">{props.title}</p>
          </div>
          <div className="at">
            <p className="m-0">{props.at}</p>
          </div>
          <div className="author">
            <p className="m-0">{props.author}</p>
          </div>
        </div>
      </a>
      <hr />
    </React.StrictMode>
  );
}

function PublicSection(props) {
  const [paperType, setPapreType] = useState("papers");
  const [paperList, setPaperList] = useState([]);

  useEffect(() => {
    // get paperList
    var newPaperList = [];
    for (let i = 1; i < props.data.length; i++) {
      if (paperType === props.data[i][0])
        newPaperList.push({
          title: props.data[i][1],
          author: props.data[i][2],
          at: props.data[i][3],
          rank: props.data[i][4],
        });
    }

    setPaperList(newPaperList);
  }, [paperType]);

  return (
    <React.StrictMode>
      <SelectBtn
        selectChange={(type) => {
          setPapreType(type);
        }}
      />
      <PublicationsList list={paperList} />
    </React.StrictMode>
  );
}
