import { CoverHalf } from "../../components/coverHalf";
import { Footer } from "../../components/footer";
import { ImgBlock } from "../../components/imgBlock";
import Loader from "react-loader-spinner";

// import peopleData from "./peopleData";

function People(props) {
  const [peopleData, setPeopleData] = useState([]);
  const [getData, setGetData] = useState(false);

  useEffect(() => {
    if (!getData) {
      // const url =
      //   "https://docs.google.com/spreadsheets/d/e/2PACX-1vQJDBlUamD2ry930iKqtY0zr9OgOADHLSnOK6PB371S0nvyS6HhBXK82L7jkWePoDz2f9noy9QUMoNz/pub?gid=0&single=true&output=csv";
      // readRemoteFile(url, {
      //   complete: (results) => {
      //     // console.log("Results:", results);
      //     setPeopleData(results.data);
      //     setGetData(true);
      //   },
      // });
      setGetData(true);
    }
  }, [getData]);

  if (getData) {
    return (
      <React.StrictMode>
        <WebNav />
        <CoverHalf />

        <PeopleSection data={peopleData} />
        <Footer />
      </React.StrictMode>
    );
  } else {
    return (
      <React.StrictMode>
        <WebNav />
        <CoverHalf />
        <div className="text-center my-5">
          <Loader type="Grid" color="#403E3E" height={80} width={80} />
        </div>
        <Footer />
      </React.StrictMode>
    );
  }
}

export { People };

function Section(props) {
  let people = props.data;

  return people.map((member) => (
    <Col xs={6} md={3}>
      <IntroductionCard
        img={member.image}
        name={member.name}
        text={member.description}
      />
    </Col>
  ));
}

function IntroductionCard(props) {
  return (
    <Card className="my-3 mx-1">
      {(() => {
        if (props.img !== "") {
          return <ImgBlock src={props.img} />;
        }
      })()}
      <Card.Body>
        <Card.Title>{props.name}</Card.Title>
        {(() => {
          if (props.text !== "") {
            return (
              <Card.Text style={{ minHeight: "1.2rem" }}>
                {props.text}
              </Card.Text>
            );
          }
        })()}
      </Card.Body>
    </Card>
  );
}

function SelectBtn(props) {
  return (
    <Container fluid>
      <Row>
        <Col xs={3}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => {
              props.selectChange("principalInvestiagtor");
            }}
          >
            <p>Principal Investiagtor</p>
          </Button>
        </Col>
        <Col xs={3}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => {
              props.selectChange("currentMember");
            }}
          >
            <p>Current Member</p>
          </Button>
        </Col>
        <Col xs={3}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => {
              props.selectChange("formerMember");
            }}
          >
            <p>Former Member</p>
          </Button>
        </Col>
        <Col xs={3}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => {
              props.selectChange("professionalCollaborators");
            }}
          >
            <p>Professional Collaborators</p>
          </Button>
        </Col>
      </Row>
    </Container>
  );
}

function PeopleList(props) {
  return (
    <React.StrictMode>
      {props.list.map((p) => (
        <Container>
          <Row>
            <div className="people-section text-center w-100">
              <div
                className="title"
                style={{
                  fontSize: "1.5rem",
                  letterSpacing: "0.3rem",
                  fontWeight: "500",
                }}
              >
                <p>{p.title}</p>
                <hr />
              </div>
            </div>
          </Row>
          <Row>
            <Section data={p.data} />
          </Row>
        </Container>
      ))}
    </React.StrictMode>
  );
}

function PeopleSection(props) {
  const [peopleType, setPeopleType] = useState("currentMember");
  const [peopleList, setPeopleList] = useState([]);

  useEffect(() => {
    if (peopleType === "principalInvestiagtor") return;

    // get
    var newPeopleList = [];
    for (let i = 1; i < props.data.length; i++) {
      if (peopleType === props.data[i][0]) {
        // search exist
        let keyExist = false;
        newPeopleList.forEach((element) => {
          if (element.title === props.data[i][1]) {
            keyExist = true;
          }
        });
        if (!keyExist) {
          newPeopleList.push({
            title: props.data[i][1],
            data: [],
          });
        }

        // add
        for (let j = 0; j < newPeopleList.length; j++) {
          if (newPeopleList[j].title === props.data[i][1]) {
            newPeopleList[j].data.push({
              name: props.data[i][2],
              image: props.data[i][3],
              description: props.data[i][4],
              time: props.data[i][5],
              project: props.data[i][6],
            });
          }
        }
      }
    }
    console.log(newPeopleList);
    setPeopleList(newPeopleList);
  }, [peopleType]);

  // render
  if (peopleType === "principalInvestiagtor") {
    return (
      <React.StrictMode>
        <SelectBtn
          selectChange={(type) => {
            setPeopleType(type);
          }}
        />
        <PrincipalInvestiagtorPage />
      </React.StrictMode>
    );
  } else {
    return (
      <React.StrictMode>
        <SelectBtn
          selectChange={(type) => {
            setPeopleType(type);
          }}
        />
        <PeopleList list={peopleList} />
      </React.StrictMode>
    );
  }
}

